.profile {
    width: 300px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
}

.avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
    margin-bottom: 10px;
}

.username {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 5px;
}

.tagline {
    font-size: 16px;
    color: #888;
}

.location {
    font-size: 18px;
    margin-top: 10px;
}

.stats {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 10px 0;
}

.stats li {
    margin-right: 20px;
    text-align: center;
}

.stats .label {
    font-size: 14px;
    color: #888;
}

.stats .quantity {
    font-size: 18px;
    font-weight: bold;
}